body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("./img/Background.jpg");
  background-size: 100%;
}

@font-face {
  font-family: 'CodersCrux2';
  src: local('CodersCrux2Regular-grnR'), url(./CodersCrux2Regular-grnR.ttf) format('typescript');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.social {
  width: auto;
  height: 50px;
  max-height: 50px;
  margin-bottom: 25px;
  margin-left: 20px;
}

p {
  max-width: 800px;
}

.home{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #4F4557;
  width: 75%;
  margin-left: auto;
  margin-right:auto;
  border-radius: 25px;
  margin-top: 200px;
}

.navigation {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  list-style-type: none;
  background-color: #6D5D6E;
  border-radius: 25px;
  padding: .75em;
  font-weight: bold;
}

a:link {
  color: #F4EEE0;
  text-decoration: none;
}

a:visited {
  color: #F4EEE0;
  text-decoration: none;
}

